import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import Home from '../views/Home.vue'
import Artcle from '../views/Article.vue'
import Hero from '../views/Hero.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        component: Main,
        children: [
            { path: '/', name: 'home', component: Home },
            { path: '/artcles/:id', component: Artcle, name: "artcles", props: true },
        ]
    },
    { path: '/heroes/:id', component: Hero, name: "heroes", props: true },

]

const router = new VueRouter({
    routes
})

export default router